    setWorkers(
      workers.map(w => {
        if (w.id === id) {
          if (field.includes('.')) {
            const [parent, child] = field.split('.');
            return {
              ...w,
              [parent]: {
                ...(w as any)[parent],
                [child]: value
              }
            };
          }
          return { ...w, [field]: value };
        }
        return w;
      })
    );
