  // ==================== VALIDACI√ìN Y NAVEGACI√ìN ====================
  const validateStep = () => {
    const currentStepInfo = steps[step - 1];
    
    if (currentStepInfo.label === "Informaci√≥n General") {
      if (!areaEspecifica || !planta || !nombreSolicitante) {
        toast({
          title: "Campos requeridos",
          description: "Por favor completa todos los campos obligatorios de Informaci√≥n General",
          variant: "destructive"
        });
        return false;
      }
      
      // Validar que al menos un tipo de trabajo est√© seleccionado
      if (!trabajoAlturas && !espaciosConfinados && !controlEnergia && 
          !izajeCargas && !trabajoCaliente && !excavaciones && !trabajoGeneral) {
        toast({
          title: "Tipo de trabajo requerido",
          description: "Debes seleccionar al menos un tipo de trabajo en las Listas de Verificaci√≥n Complementarias",
          variant: "destructive"
        });
        return false;
      }
    }
    
    // Validaciones adicionales para otros steps seg√∫n sea necesario
    
    return true;
  };

  const handleNext = () => {
    if (!validateStep()) return;
    
    if (step < steps.length) {
      setStep(step + 1);
    }
  };

  const handlePrevious = () => {
    if (step > 1) {
      setStep(step - 1);
    }
  };

