// Mapeo actualizado de tipos de trabajo seg√∫n documentaci√≥n
const workTypesMap: {[key: string]: string} = {
  'trabajoAlturas': 'Trabajo en Alturas',
  'espaciosConfinados': 'Espacios Confinados',
  'controlEnergia': 'Control de Energ√≠as',
  'izajeCargas': 'Izaje de Cargas',
  'trabajoCaliente': 'Trabajo en Caliente',
  'excavaciones': 'Excavaciones',
  'trabajoGeneral': 'Trabajo General',
  // Mantener compatibilidad con nomenclatura anterior
  'altura': 'Trabajo en Alturas',
  'confinado': 'Espacios Confinados',
  'energia': 'Control de Energ√≠as',
  'izaje': 'Izaje de Cargas',
  'caliente': 'Trabajo en Caliente',
  'excavacion': 'Excavaciones',
  'general': 'Trabajo General'
};

const getWorkTypesString = (permit: any): string => {
  // Priorizar los nuevos campos booleanos
  const workTypes: string[] = [];
  
  if (permit.trabajoAlturas) workTypes.push('Trabajo en Alturas');
  if (permit.espaciosConfinados) workTypes.push('Espacios Confinados');
  if (permit.controlEnergia) workTypes.push('Control de Energ√≠as');
  if (permit.izajeCargas) workTypes.push('Izaje de Cargas');
  if (permit.trabajoCaliente) workTypes.push('Trabajo en Caliente');
  if (permit.excavaciones) workTypes.push('Excavaciones');
  if (permit.trabajoGeneral) workTypes.push('Trabajo General');
  
  // Fallback al campo workType si existe (compatibilidad)
  if (workTypes.length === 0 && permit.workType && Array.isArray(permit.workType)) {
    return permit.workType.map((key: string) => workTypesMap[key] || key).join(', ');
  }
  
  return workTypes.length > 0 ? workTypes.join(', ') : 'Trabajo General';
};

const getStatusText = (status: string) => {
  const statusText: {[key: string]: string} = {
    'borrador': 'Borrador',
    'pendiente_revision': 'Pendiente de Revisi√≥n',
    'aprobado': 'Aprobado',
    'en_ejecucion': 'En Ejecuci√≥n',
    'suspendido': 'Suspendido',
    'cerrado': 'Cerrado',
    'rechazado': 'Rechazado'
  };
  return statusText[status] || status;
};

const signatureRoles: { [key in UserRole | 'sst' | 'mantenimiento']: string } = {
  solicitante: 'QUIEN SOLICITA (JEFES Y DUE√ëOS DE AREA)',
  autorizante: 'QUIEN AUTORIZA (L√çDER A CARGO DEL EQUIPO EJECUTANTE)',
  mantenimiento: 'PERSONAL DE MANTENIMIENTO',
  sst: 'AREA SST (si aplica)',
  lider_tarea: 'L√≠der de la Tarea',
  ejecutante: 'Ejecutante del Trabajo',
  lider_sst: 'L√≠der SST',
  admin: 'Administrador'
};

type PermitCreateData = Omit<Permit, 'id' | 'createdAt' | 'status' | 'createdBy' | 'number' | 'user'> & {
  userId: string;
  userDisplayName: string | null;
  userEmail: string | null;
  userPhotoURL: string | null;
};

export async function createPermit(data: PermitCreateData) {
  if (!data.userId) {
    return { success: false, error: 'User not authenticated' };
  }

  const { userId, userDisplayName, userEmail, userPhotoURL, ...permitData } = data;

  const initialApprovals = {
    solicitante: { status: 'pendiente' as const },
    autorizante: { status: 'pendiente' as const },
    mantenimiento: { status: 'pendiente' as const },
    sst: { status: 'pendiente' as const }
  };

  // Preparar el payload del permiso con la nueva estructura
  const permitPayload: Partial<Permit> = {
    ...permitData,
    status: 'pendiente_revision' as const,
    createdBy: userId,
    createdAt: FieldValue.serverTimestamp(),
    fechaCreacion: FieldValue.serverTimestamp(),
    user: {
      displayName: userDisplayName,
      email: userEmail,
      photoURL: userPhotoURL,
    },
    approvals: initialApprovals,
    closure: {},
  };

  // Convertir fechas a Timestamp si existen
  if (permitData.fechaInicio) {
    permitPayload.fechaInicio = FieldValue.serverTimestamp();
  }
  if (permitData.fechaFin) {
    permitPayload.fechaFin = FieldValue.serverTimestamp();
  }

  // Solo incluir anexos si los tipos de trabajo correspondientes est√°n seleccionados
  if (permitData.trabajoAlturas && permitData.anexoAlturas) {
    permitPayload.anexoAlturas = permitData.anexoAlturas;
  }
  
  if (permitData.espaciosConfinados && permitData.anexoConfinado) {
    permitPayload.anexoConfinado = permitData.anexoConfinado;
  }
  
  if (permitData.controlEnergia && permitData.anexoEnergias) {
    permitPayload.anexoEnergias = permitData.anexoEnergias;
  }
  
  if (permitData.izajeCargas && permitData.anexoIzaje) {
    permitPayload.anexoIzaje = permitData.anexoIzaje;
  }
  
  if (permitData.excavaciones && permitData.anexoExcavaciones) {
    permitPayload.anexoExcavaciones = permitData.anexoExcavaciones;
  }

  // Mantener compatibilidad con anexoATS si existe
  if (permitData.anexoATS) {
    permitPayload.anexoATS = permitData.anexoATS;
  }

  // Mantener compatibilidad con workType para sistemas legacy
  if (permitData.workType && permitData.workType.length > 0) {
    permitPayload.workType = permitData.workType;
  }

  try {
    const docRef = await adminDb.collection('permits').add(permitPayload as any);
    const permitNumber = `PT-${Date.now()}-${docRef.id.substring(0, 6).toUpperCase()}`;
    await docRef.update({ 
      number: permitNumber,
      numeroPermiso: permitNumber 
    });
    
    console.log('‚úÖ [Action] Permiso creado con √©xito en Firestore:', docRef.id);

    // Obtener tipos de trabajo para la notificaci√≥n
    const workTypesText = getWorkTypesString(permitPayload);
    const baseUrl = process.env.NEXT_PUBLIC_BASE_URL || 'https://sgpt-movil.web.app';
    const permitUrl = `${baseUrl}/permits/${docRef.id}`;
    
    const messageBody = `*¬°Alerta de Seguridad SGPT!* üö®
Se ha creado una nueva solicitud de permiso de trabajo.

üìÑ *N√∫mero:* ${permitNumber}
üë§ *Solicitante:* ${userDisplayName || 'N/A'}
üìç *√Årea:* ${permitData.areaEspecifica || 'N/A'}
üè≠ *Planta:* ${permitData.planta || 'N/A'}
üõ†Ô∏è *Tipo de Trabajo:* ${workTypesText}
üìù *Descripci√≥n:* ${permitData.descripcionTarea?.substring(0, 100) || 'N/A'}${permitData.descripcionTarea && permitData.descripcionTarea.length > 100 ? '...' : ''}

Por favor, revise la solicitud para su aprobaci√≥n en el siguiente enlace:
${permitUrl}`;
    
    await sendWhatsAppNotification(messageBody);
    
    revalidatePath('/permits');
    revalidatePath('/dashboard');
    
    return { success: true, permitId: docRef.id, permitNumber };
  } catch (error: any) {
    console.error("‚ùå [Action] Error al crear permiso:", error);
    return { 
      success: false, 
      error: error.message || 'Could not create permit. Please try again.' 
    };
  }
}

export async function addSignatureAndNotify(
  permitId: string, 
  role: 'solicitante' | 'autorizante' | 'mantenimiento' | 'sst', 
  signatureType: 'firmaApertura' | 'firmaCierre',
  signatureDataUrl: string,
  user: { uid: string, displayName: string | null }
) {
  if (!permitId || !role || !signatureDataUrl || !user) {
    return { success: false, error: 'Faltan par√°metros para guardar la firma.' };
  }

  try {
    const docRef = adminDb.collection('permits').doc(permitId);
    
    const signaturePath = `approvals.${role}.${signatureType}`;
    const statusPath = `approvals.${role}.status`;
    const userIdPath = `approvals.${role}.userId`;
    const userNamePath = `approvals.${role}.userName`;
    const signedAtPath = `approvals.${role}.signedAt`;

    const updateData: { [key: string]: any } = {
      [signaturePath]: signatureDataUrl,
      [`${userNamePath}`]: user.displayName,
      [`${userIdPath}`]: user.uid,
    };

    if (signatureType === 'firmaApertura') {
      updateData[statusPath] = 'aprobado';
      updateData[signedAtPath] = FieldValue.serverTimestamp();
    }

    await docRef.update(updateData);
    
    // Fetch permit for notification
    const permitDoc = await docRef.get();
    const permitData = permitDoc.data() as Permit;
    
    const signatureRoleName = (signatureRoles as any)[role] || role;
    const baseUrl = process.env.NEXT_PUBLIC_BASE_URL || 'https://sgpt-movil.web.app';
    const permitUrl = `${baseUrl}/permits/${permitId}`;
    
    const workTypesText = getWorkTypesString(permitData);
    
    const messageBody = `*Notificaci√≥n de Firma - SGPT* üñãÔ∏è
El permiso *${permitData.number || permitData.numeroPermiso || permitId}* ha sido firmado.

üë§ *Qui√©n firm√≥:* ${user.displayName || 'N/A'}
‚ú® *Rol:* ${signatureRoleName}
‚úçÔ∏è *Tipo de firma:* ${signatureType === 'firmaApertura' ? 'Apertura' : 'Cierre'}
üìç *√Årea:* ${permitData.areaEspecifica || 'N/A'}
üõ†Ô∏è *Tipo de Trabajo:* ${workTypesText}

Puede ver los detalles aqu√≠:
${permitUrl}`;
    
    await sendWhatsAppNotification(messageBody);

    revalidatePath(`/permits/${permitId}`);
    return { success: true };

  } catch (error: any) {
    console.error("‚ùå Error al guardar firma y notificar:", error);
    return {
      success: false,
      error: error.message || 'No se pudo guardar la firma.'
    };
  }
}

export async function updatePermitStatus(
  permitId: string, 
  status: PermitStatus, 
  reason?: string, 
  closureData?: Partial<PermitClosure>
) {
  if (!permitId) {
    return { success: false, error: 'Permit ID is required.' };
  }

  try {
    const docRef = adminDb.collection('permits').doc(permitId);
    const updateData: { 
      status: PermitStatus; 
      rejectionReason?: string; 
      closure?: Partial<PermitClosure> | FieldValue 
    } = { status };

    if (status === 'rechazado' && reason) {
      updateData.rejectionReason = reason;
    }

    if (status === 'cerrado') {
      updateData.closure = {
        ...(closureData || {}),
        fechaCierre: FieldValue.serverTimestamp(),
      };
    }

    await docRef.update(updateData);

    // Fetch permit for notification
    const permitDoc = await docRef.get();
    const permitData = permitDoc.data() as Permit;

    const statusText = getStatusText(status);
    const workTypesText = getWorkTypesString(permitData);
    const baseUrl = process.env.NEXT_PUBLIC_BASE_URL || 'https://sgpt-movil.web.app';
    const permitUrl = `${baseUrl}/permits/${permitId}`;

    let messageBody = `*Actualizaci√≥n de Estado - SGPT* üîÑ
El estado del permiso *${permitData.number || permitData.numeroPermiso || permitId}* ha cambiado.

*Nuevo Estado:* ${statusText}
üìç *√Årea:* ${permitData.areaEspecifica || 'N/A'}
üè≠ *Planta:* ${permitData.planta || 'N/A'}
üõ†Ô∏è *Tipo de Trabajo:* ${workTypesText}

Puede ver los detalles aqu√≠:
${permitUrl}`;

    if (status === 'rechazado' && reason) {
      messageBody += `\n\n*Motivo del rechazo:* ${reason}`;
    }
    
    if (status === 'cerrado' && closureData) {
      messageBody += `\n\n*Informaci√≥n de cierre:*`;
      if (closureData.areaDespejada) {
        messageBody += `\n‚Ä¢ √Årea despejada: ${closureData.areaDespejada}`;
      }
      if (closureData.informoResponsable) {
        messageBody += `\n‚Ä¢ Inform√≥ al responsable: ${closureData.informoResponsable}`;
      }
    }
    
    await sendWhatsAppNotification(messageBody);
    
    revalidatePath(`/permits/${permitId}`);
    revalidatePath('/permits');
    revalidatePath('/dashboard');

    return { success: true };
  } catch (error: any) {
    console.error("‚ùå Error updating permit status:", error);
    return {
      success: false,
      error: error.message || 'Could not update permit status.'
    };
  }
}

// Nueva funci√≥n para actualizar campos espec√≠ficos del permiso
export async function updatePermitData(
  permitId: string,
  updateData: Partial<Permit>
) {
  if (!permitId) {
    return { success: false, error: 'Permit ID is required.' };
  }

  try {
    const docRef = adminDb.collection('permits').doc(permitId);
    
    // Limpiar datos undefined
    const cleanData = Object.fromEntries(
      Object.entries(updateData).filter(([_, v]) => v !== undefined)
    );

    await docRef.update(cleanData);
    
    revalidatePath(`/permits/${permitId}`);
    revalidatePath('/permits');
    
    return { success: true };
  } catch (error: any) {
    console.error("‚ùå Error updating permit data:", error);
    return {
      success: false,
      error: error.message || 'Could not update permit data.'
    };
  }
}

// Nueva funci√≥n para validaci√≥n diaria
export async function addDailyValidation(
  permitId: string,
  anexoType: 'anexoAlturas' | 'anexoConfinado' | 'anexoExcavaciones',
  validationType: 'autoridad' | 'responsable',
  validationData: {
    dia: number;
    fecha: string;
    nombre: string;
    firma: string;
  }
) {
  if (!permitId || !anexoType || !validationType) {
    return { success: false, error: 'Faltan par√°metros requeridos.' };
  }

  try {
    const docRef = adminDb.collection('permits').doc(permitId);
    const permitDoc = await docRef.get();
    const permitData = permitDoc.data() as Permit;

    if (!permitData[anexoType]) {
      return { success: false, error: `El anexo ${anexoType} no existe en este permiso.` };
    }

    const anexo = permitData[anexoType] as any;
    
    if (!anexo.validacionDiaria) {
      anexo.validacionDiaria = [];
    }

    // Agregar la validaci√≥n
    anexo.validacionDiaria.push(validationData);

    await docRef.update({
      [anexoType]: anexo
    });

    revalidatePath(`/permits/${permitId}`);
    
    return { success: true };
  } catch (error: any) {
    console.error("‚ùå Error adding daily validation:", error);
    return {
      success: false,
      error: error.message || 'No se pudo agregar la validaci√≥n diaria.'
    };
  }
}

// Nueva funci√≥n para agregar trabajadores
export async function addWorkerToPermit(
  permitId: string,
  worker: ExternalWorker
) {
  if (!permitId || !worker) {
    return { success: false, error: 'Faltan par√°metros requeridos.' };
  }

  try {
    const docRef = adminDb.collection('permits').doc(permitId);
    
    await docRef.update({
      workers: FieldValue.arrayUnion(worker)
    });

    revalidatePath(`/permits/${permitId}`);
    
    return { success: true };
  } catch (error: any) {
    console.error("‚ùå Error adding worker:", error);
    return {
      success: false,
      error: error.message || 'No se pudo agregar el trabajador.'
    };
  }
}

// Nueva funci√≥n para eliminar trabajadores
export async function removeWorkerFromPermit(
  permitId: string,
  workerCedula: string
) {
  if (!permitId || !workerCedula) {
    return { success: false, error: 'Faltan par√°metros requeridos.' };
  }

  try {
    const docRef = adminDb.collection('permits').doc(permitId);
    const permitDoc = await docRef.get();
    const permitData = permitDoc.data() as Permit;

    if (!permitData.workers) {
      return { success: false, error: 'No hay trabajadores en este permiso.' };
    }

    const updatedWorkers = permitData.workers.filter(
      (w: ExternalWorker) => w.cedula !== workerCedula
    );

    await docRef.update({
      workers: updatedWorkers
    });

    revalidatePath(`/permits/${permitId}`);
    
    return { success: true };
  } catch (error: any) {
    console.error("‚ùå Error removing worker:", error);
    return {
      success: false,
      error: error.message || 'No se pudo eliminar el trabajador.'
    };
  }
}
